.container {
  ul {
    li {
      font-size: 40px;
      .name {
        color: royalblue;
      }
      .age {
        color: orange;
      }
    }
  }
}

.btn {
  position: absolute;
  &.active {
    color: red;
  }
}

list {
  li {
    &:list-child {
      margin-right: 0;
    }
  }
}

.box {
  font: {
    weight: bold;
    size: 10px;
    family: sans-serif;
  }
  margin: {
    top: 10px;
    left: 20px
  }
  padding: {
    top: 10px;
    left: 20px;
    right: 30px;
  }
}

.container {
  position: fixed;
  top: 100px;
  .item {
    width: 100px;
    height: 100px;
    transform: translateX(100px);
  }
}

div {
  $size: 30px;
  width: 20px + 20px;
  height: 40px - 10px;
  font-size: 10px * 2;
  margin: 10px + 12px / 2;
  padding: 20px % 7;
}

span {
    font-size: 10px;
    line-height: 10px;
    font-family: serif;
    font: 10px / 10px serif;
}

.box {
  background-color: royalblue;
  width: calc(100% - 200px);
  height: 100px;
}